/** axios
  * 
  */
import axios from 'axios'
// import router from './router'
import NProgress from 'nprogress'
import Event from './event'
export const emitter = new Event()

// 
if (process.env.NODE_ENV === 'development') {
  // axios.defaults.baseURL = '/api'
  axios.defaults.baseURL = 'http://localhost:5500/static/data'
} else if (process.env.NODE_ENV === 'debug') {
  axios.defaults.baseURL = ''
} else if (process.env.NODE_ENV === 'production') {
  axios.defaults.baseURL = '/client-api'
}

/**
* 
* @param {Number} status 
*/
const errorHandle = (status, other) => {
  // 
  switch (status) {
    case 403:
      // ''
      emitter.emit('noPermission')
      break
    case 404:
      // ''
      emitter.emit('noLogin')
      break
    case 511:
      // ''
      emitter.emit('noLogin')
      break
    default:
      emitter.emit('errorOther')
  }
}

/**
* 
* @param {String} code 
*/
const successHandle = (code) => {
  // 
  switch (code) {
    case '500':
      // 
      emitter.emit('paramError')
      break
    case '900':
      emitter.emit('requestError')
      break
    default:
  }
}

// 
axios.defaults.timeout = 10000

// 
axios.defaults.headers = {'Content-Type': 'application/json; charset=utf-8'}

/**
  * 
  * tokentoken
  */
// axios.interceptors.request.use(
//   config => {
//     // token
//     // tokentokentoken
//     // token
//     // 
//     const token = store.state.token
//       token && (config.headers.Authorization = token)
//       return config
//   },
//   error => Promise.error(error)
// )

// 
axios.interceptors.response.use(
  // 
  response => {
    // 200
    // 
    if (response.status === 200) {
      successHandle(response.data.code)
      return Promise.resolve(response)
    }
    // else {
    //   return Promise.reject(response)
    // }
  },
  // 
  error => {
    const { response } = error
    if (response) {
      // 2xx
      errorHandle(response.status, response.data.message)
      return Promise.reject(response)
    } else {
      // 
      // eg:statenetwork
      // networkapp.vue
      // 
      emitter.emit('errorServer')
    }
  }
)
export default {
/**
   * getget
   * @param {String} url [url]
   * @param {Object} params []
   */
  get (url, params) {
    NProgress.start()
    return new Promise((resolve, reject) => {
      axios.get(url, params)
        .then(res => {
          NProgress.done()
          resolve(res)
          // if (res && callback && typeof callback === 'function') {
          //   callback(res)
          // }
        })
        .catch(err => {
          reject(err)
        })
    })
  },
  /**
     * postpost
     * @param {String} url [url]
     * @param {Object} params []
     */
  post (url, params) {
    NProgress.start()
    return new Promise((resolve, reject) => {
      axios.post(url, JSON.stringify(params))
        .then(res => {
          NProgress.done()
          resolve(res)
          // if (res && callback && typeof callback === 'function') {
          //   callback(res)
          // }
        })
        .catch(err => {
          reject(err)
        })
    })
  }
}



